import {
  AdjustmentsVerticalIcon,
  ClipboardIcon,
} from "@heroicons/react/24/outline";
import { Button, Input, message, Modal, Slider } from "antd";
import * as React from "react";
import { ControlRowView, SecondaryButton } from "../../../atoms";
import { IGeneratorConfig, IStatus } from "../../../types";
import { truncateText } from "../../../utils";

const PromptPreviews = ({
  selectedPrompt,
  setError,
}: {
  selectedPrompt: string;
  setError: (error: IStatus) => void;
}) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);

  const serverUrl = process.env.GASTSBY_API_SERVER + "/api";
  const [loading, setLoading] = React.useState(false);

  const [previews, setPreviews] = React.useState<any[] | null>(null);

  const previewList = previews?.map((image, index) => {
    return (
      <div
        onClick={() => {
          // copy image.prompt to clipboard
          navigator.clipboard.writeText(image.prompt);
          message.success("Copied to clipboard");
        }}
        role={"button"}
        key={index}
        className="w-full h-auto block relative"
      >
        <img
          src={image.src}
          className="object-cover mb-2 rounded hover:brightness-75 transition duration-300 "
        />
        <div className=" bottom-0 text-sm pb-2">
          {truncateText(image.prompt, 50)}
        </div>
      </div>
    );
  });

  const fetchPreviews = (query: string) => {
    console.log("fetching previews");
    setLoading(true);
    fetch(`${serverUrl}/preview`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        prompt: query,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        setLoading(false);
        if (data.status) {
          setPreviews(data.result.images);
          console.log(data);
        } else {
          setError({
            status: false,
            message: JSON.stringify(data),
          });
        }
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
        setError({
          status: false,
          message: JSON.stringify(err),
        });
      });
  };

  return (
    <div className="text-secondary rounded ">
      <Modal
        width={"70%"}
        // bodyStyle={{ width: "600px", maxWidth: "500px" }}
        title={
          <span>
            <AdjustmentsVerticalIcon className="h-4 text-accent inline-block mr-2 text-sm -mt-1" />
            Suggestions and Previews
          </span>
        }
        open={isModalVisible}
        onCancel={() => {
          setIsModalVisible(false);
        }}
        onOk={() => {
          setIsModalVisible(false);
        }}
        footer={[
          <Button
            onClick={() => {
              setIsModalVisible(false);
            }}
            key="close"
            className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
            type="primary"
          >
            close
          </Button>,
        ]}
      >
        <div className="mb-4">Prompt suggestions, powered by lexica.art</div>

        {loading && <div> Fetching previews from lexica.art ...</div>}

        {previews && (
          <div className="columns-2 md:columns-3 lg:columns-4">
            {previewList}
          </div>
        )}
      </Modal>

      <div
        onClick={() => {
          setIsModalVisible(true);
          fetchPreviews(selectedPrompt);
        }}
        role="button"
        className="text-sm text-right hover:text-accent transition duration-300"
      >
        Preview prompt with suggestions?
      </div>
    </div>
  );
};
export default PromptPreviews;
