import {
  AdjustmentsVerticalIcon,
  Cog6ToothIcon,
  Cog8ToothIcon,
  ExclamationCircleIcon,
  PhotoIcon,
} from "@heroicons/react/24/outline";
import * as React from "react";
import { CollapseBox, LaunchButton, SecondaryButton } from "../../../atoms";
import {
  IGeneratorConfig,
  IGeneratorResult,
  IStatus,
  IUIConfig,
} from "../../../types";
import GeneratorControlsView from "../controls";
import NegativePromptsView from "./negprompts";
import SamplesView from "../samples";
import PositivePromptsView from "./postprompts";
import ImagePromptsView from "./imageprompt";
import ImageUploadView from "./imageupload";
import SettingsView from "./settings";
import PromptPreviews from "./previews";

const PromptsControlView = ({
  config,
  setConfig,
  uiConfig,
  setUiConfig,
  setGenerations,
  scratchPad,
  setScratchPad,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  uiConfig: IUIConfig;
  setUiConfig: (config: IUIConfig) => void;
  generations: any;
  setGenerations: (generations: IGeneratorResult | null) => void;
  scratchPad: string[];
  setScratchPad: (scratchPad: string[]) => void;
}) => {
  const [selectedPrompt, setSelectedPrompt] = React.useState(0);
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<null | IStatus>(null);

  const promptInputRef = React.useRef<HTMLTextAreaElement>(null);
  const serverUrl = process.env.GASTSBY_API_SERVER + "/api";

  const errorMsg: IStatus = {
    status: false,
    message:
      "Error fetching generation. There is a chance the server is down. Visit the peacasso repo to learn more about running your own server",
  };

  React.useEffect(() => {
    promptInputRef.current?.focus();
    promptInputRef.current!.value = config.prompt[selectedPrompt];
  }, [selectedPrompt]);

  const updateConfigWithPrompt = () => {
    if (promptInputRef.current && !loading) {
      const holder = config.prompt;
      holder[selectedPrompt] = promptInputRef.current.value;
      setConfig({ ...config, prompt: holder });
      promptInputRef.current.blur();
    }
  };

  const setPromptFromSample = (prompt: string) => {
    promptInputRef.current!.value = prompt;
    const holder = config.prompt;
    holder[selectedPrompt] = prompt;
    setConfig({ ...config, prompt: holder });
  };

  // fetch generation
  const fetchGeneration = (config: IGeneratorConfig) => {
    setGenerations(null);
    setError(null);
    setLoading(true);
    const generateUrl = `${serverUrl}/generate`;

    const postData = {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ type: "generate", config: config }),
    };
    fetch(generateUrl, postData)
      .then((res) => {
        if (res.status === 200) {
          return res.json();
        } else {
          setError({
            status: false,
            message: `Error ${res.status} c: ${res.statusText}`,
          });
          setLoading(false);
        }
      })
      .then((data) => {
        if (data) {
          if (data.status) {
            setGenerations(data.result);
            setLoading(false);
          } else {
            console.log(data.status_message);
            setError({
              status: false,
              message: data.status_message,
            });
            setLoading(false);
          }
        }
      })
      .catch(() => {
        setError(errorMsg);
        setLoading(false);
      });
  };

  const advancedCallback = (val: boolean) => {
    setUiConfig({ ...uiConfig, advancedOptions: val });
  };

  return (
    <div className="">
      {/*  Generator Results */}
      <div className="text-primary text-sm mb-4 font-semibpold">
        Write a prompt to generate result.
      </div>
      {/* prompt input box and generate button  */}

      <div className="  flex relative  ">
        <div className="flex-1 ">
          {" "}
          <>
            <div
              className={`${
                loading ? " pointer-events-none" : ""
              }     gap-2 md:flex `}
            >
              {config.init_image && (
                <ImageUploadView setConfig={setConfig} config={config} />
              )}

              <div className="flex-1 ">
                <textarea
                  style={{ height: "96px" }}
                  className={` w-full p-3   text-gray-500 text-sm rounded border border-gray-400 inline-block  ${
                    loading ? "brightness-90 pointer-events-none" : ""
                  }`}
                  placeholder="Enter Prompt"
                  defaultValue={config.prompt}
                  ref={promptInputRef}
                  onKeyDown={(e) => {
                    if (
                      e.key === "Enter" &&
                      promptInputRef.current &&
                      !loading
                    ) {
                      updateConfigWithPrompt();
                    }
                  }}
                  onBlur={() => {
                    updateConfigWithPrompt();
                  }}
                />
                <PromptPreviews
                  selectedPrompt={config.prompt[selectedPrompt]}
                  setError={setError}
                />
              </div>

              <div className="gap-2 flex flex-col mt-2 md:mt-0 ">
                <LaunchButton
                  onClick={() => {
                    fetchGeneration(config);
                  }}
                >
                  <div className="   ">
                    {loading && (
                      <div className="inline-block   ">
                        <Cog6ToothIcon className="relative -pb-2  animate-spin  inline-flex rounded-full h-5 w-5" />{" "}
                        Generating ..
                      </div>
                    )}
                    {!loading && (
                      <span className="inline-block ">
                        {" "}
                        <PhotoIcon className="inline-block h-5 mr-2 -mt-1" />
                        {/* <Icon icon="app" size={6} /> */}
                        Generate
                      </span>
                    )}
                  </div>{" "}
                </LaunchButton>
                <SettingsView
                  config={config}
                  setConfig={setConfig}
                  generate={fetchGeneration}
                  isLoading={loading}
                />
              </div>
            </div>
          </>
        </div>
      </div>

      {/* prompt controls add prompt  */}
      <div className="mt-4">
        <CollapseBox
          callback={advancedCallback}
          open={uiConfig.advancedOptions}
          title={
            <div>
              <AdjustmentsVerticalIcon className="h-4 inline-block text-accent" />{" "}
              Advanced Prompt Controls
            </div>
          }
        >
          <div className="   text-primary     ">
            {/* positive prompts  */}
            <PositivePromptsView
              config={config}
              setConfig={setConfig}
              uiConfig={uiConfig}
              setUiConfig={setUiConfig}
              selectedPrompt={selectedPrompt}
              setSelectedPrompt={setSelectedPrompt}
            />
            {/* negative prompts  */}
            <NegativePromptsView config={config} setConfig={setConfig} />
            <ImagePromptsView
              config={config}
              setConfig={setConfig}
              error={error}
              setError={setError}
              scratchPad={scratchPad}
              setScratchPad={setScratchPad}
            />
          </div>
        </CollapseBox>

        {loading && (
          <>
            <div className="rounded bg-secondary mt-4 p-3">
              <span className="inline-block h-6 w-6 relative mr-2">
                <Cog8ToothIcon className="animate-ping text-accent absolute inline-flex h-full w-full rounded-ful  opacity-75" />
                <Cog8ToothIcon className="relative text-accent animate-spin  inline-flex rounded-full h-6 w-6" />
              </span>
              Working .... generations may take a while, depending on the
              capacity of your GPU.
            </div>
            <div className="relative">
              <div className="loadbar rounded-b"></div>
            </div>
          </>
        )}
      </div>

      {/* samples view  */}
      <div className="flex gap-3 mt-4">
        <div className={`flex-1 mt-3 ${loading ? "pointer-events-none" : ""}`}>
          <SamplesView setPrompt={setPromptFromSample} />
        </div>
      </div>

      {error && !error.status && (
        <div className="mt-6">
          {" "}
          <div className="font-semibold  pb-1 mb-2 text-red-400">
            <ExclamationCircleIcon className="inline-block h-6 w-6 -mt-1" /> An
            error occurred. Please try again later.
          </div>
          <div className="text-secondpary border-red-400 border rounded bg-secondary p-2 px-3">
            {" "}
            {error.message}.
          </div>
        </div>
      )}
    </div>
  );
};
export default PromptsControlView;
