import {
  ExclamationTriangleIcon,
  XMarkIcon,
} from "@heroicons/react/24/outline";
import * as React from "react";
import { IGeneratorConfig } from "../../../types";
import AddButton from "./addbutton";

const NegativePromptsView = ({
  config,
  setConfig,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
}) => {
  const negpromptsList = config.negative_prompt?.map(
    (prompt: string, i: number) => {
      return (
        <div
          className="   hover:brightness-90 duration-300 bg-secondary text-sm rounded"
          key={"negpromptrow" + i}
        >
          <div className="inline-block p-2 cursor-default  px-3">{prompt}</div>
          <div
            role={"button"}
            onClick={() => {
              setConfig({
                ...config,
                negative_prompt: config.negative_prompt?.filter(
                  (_, j) => j !== i
                ),
              });
            }}
            className="inline-block p-1"
          >
            <XMarkIcon className="inline-block h-5 pr-1" />
          </div>
        </div>
      );
    }
  );

  const AddButtonClick = (prompt: string = "") => {
    setConfig({
      ...config,
      negative_prompt: [...(config.negative_prompt || []), prompt],
    });
  };

  return (
    <div className=" pb-3 border-b  border-dashed   mt-4">
      <div className="text-sm mb-2">
        <ExclamationTriangleIcon className="h-4 text-accentp mr-1 inline-block" />
        Negative Prompts
        <div className="text-xs mt-1 text-secondary">
          {" "}
          Add concepts you want to exclude from the generated image.
        </div>
      </div>

      <div className="flex gap-2">
        <div>
          <div className="inline-flex flex-wrap gap-2">
            {negpromptsList}
            <AddButton
              config={config}
              setConfig={setConfig}
              configField={"negative_prompt"}
              onClick={AddButtonClick}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
export default NegativePromptsView;
