import {
  ChevronDownIcon,
  ChevronUpIcon,
  XMarkIcon,
} from "@heroicons/react/24/outline";
import React, { ReactChildren, ReactChild, useRef } from "react";
import Icon from "./icons";

interface IProps {
  children?: ReactChild | ReactChildren;
  title?: string | ReactChild;
  subtitle?: string | ReactChild | ReactChildren;
  count?: number;
  active?: boolean;
  cursor?: string;
  icon?: ReactChild;
  padding?: string;
  className?: string;
  open?: boolean;
  hoverable?: boolean;
  onClick?: () => void;
  callback?: (val: any) => void;
}

export const SectionHeader = ({
  children,
  title,
  subtitle,
  count,
  icon,
}: IProps) => {
  return (
    <div className="mb-4">
      <h1 className="text-primary text-2xl">
        {/* {count !== null && <span className="text-accent mr-1">{count}</span>} */}
        {icon && <>{icon}</>}
        {title}
        {count !== null && (
          <span className="text-accent mr-1 ml-2 text-xs">{count}</span>
        )}
      </h1>
      {subtitle && <span className="inline-block">{subtitle}</span>}
      {children}
    </div>
  );
};

export const IconButton = ({
  onClick,
  icon,
  className,
  active = false,
}: IProps) => {
  return (
    <span
      role={"button"}
      onClick={onClick}
      className={`inline-block mr-2 hover:text-accent transition duration-300 ${className} ${
        active ? "border-accent border rounded text-accent" : ""
      }`}
    >
      {icon}
    </span>
  );
};

export const LaunchButton = ({ children, onClick }: any) => {
  return (
    <button
      role={"button"}
      className={` w-full    focus:ring ring-accent  ring-l-none  p-3 px-5 rounded  cursor-pointer hover:brightness-110 bg-accent transition duration-500    text-white`}
      onClick={onClick}
    >
      {children}
    </button>
  );
};

export const ControlRowView = ({
  title,
  description,
  value,
  control,
  className,
}: any) => {
  return (
    <div className={`${className}`}>
      <div>
        {title && <span className="text-primary inline-block">{title} </span>}
        {value && (
          <span className="text-xs ml-1 text-accent -mt-2 inline-block">
            {value}
          </span>
        )}
      </div>
      {description && (
        <div className="text-secondary text-xs"> {description} </div>
      )}
      {control}
    </div>
  );
};

export const SecondaryButton = ({
  children,
  onClick,
  className = "p-3 px-5",
}: any) => {
  return (
    <button
      role={"button"}
      className={` ${className}   focus:ring ring-accent   rounded  cursor-pointer hover:brightness-110 bg-secondary transition duration-500    text-primary`}
      onClick={onClick}
    >
      {children}
    </button>
  );
};

export const Card = ({
  children,
  title,
  subtitle,
  hoverable = true,
  active,
  cursor = "cursor-pointer",
  padding = "p-3",
}: IProps) => {
  let border = active
    ? "border-indigo-600"
    : "border-gray-100 hover:border-indigo-600 ";
  border = hoverable ? border : "border-gray-100";

  return (
    <div
      className={`${border} border-2 bg-gray-100  group ${padding} rounded ${cursor} transition duration-300`}
    >
      <div className="mt- text-sm text-secondary  break-words">
        {title && (
          <div className="text-accent rounded font-semibold  text-xs pb-1">
            {title}
          </div>
        )}
        <div>{subtitle}</div>
      </div>
    </div>
  );
};

export const CollapseBox = ({
  title,
  subtitle,
  children,
  className = " p-3",
  open = false,
  callback,
}: IProps) => {
  const [isOpen, setIsOpen] = React.useState<boolean>(open);
  const chevronClass = "h-4 cursor-pointer inline-block mr-1";
  return (
    <div
      onMouseDown={(e) => {
        if (e.detail > 1) {
          e.preventDefault();
        }
      }}
      className="bordper border-secondary rounded"
    >
      <div
        onClick={() => {
          callback(!isOpen);
          setIsOpen(!isOpen);
        }}
        className={`cursor-pointer bg-secondary p-2 rounded ${
          isOpen && open ? "rounded-b-none " : " "
        }"}`}
      >
        {isOpen && open && <ChevronUpIcon className={chevronClass} />}
        {!(isOpen && open) && <ChevronDownIcon className={chevronClass} />}

        <span className=" inline-block -mt-2 mb-2 text-xs">
          {" "}
          {/* {isOpen ? "hide" : "show"} section |  */}
          {title}
        </span>
      </div>

      {isOpen && open && (
        <div className={`${className} bg-light  rounded rounded-t-none`}>
          {children}
        </div>
      )}
    </div>
  );
};

export const LoadBox = ({
  subtitle,
  className = "my-2 text-accent ",
}: IProps) => {
  return (
    <div className={`${className} `}>
      {" "}
      <span className="mr-2 ">
        {" "}
        <Icon size={5} icon="loading" />
      </span>{" "}
      {subtitle}
    </div>
  );
};

export const LoadingBar = ({}: IProps) => {
  return (
    <div className="relative h-4 w-full border">
      {" "}
      <div className=" progress"></div>
    </div>
  );
};

export const FloatingHeaderBox = ({ title, className, children }: IProps) => {
  return (
    <div
      className={`mt-6 p-2 bg-secondary relative   rounded border-secondary ${className}`}
    >
      <div className="-mt-7 border-secondary p-1 px-4 rounded bg-secondary absolute text-sm   inline-block ml-2">
        {title}
      </div>
      <div className="mt-1">{children}</div>
    </div>
  );
};

export const MessageBox = ({ title, children, className }: IProps) => {
  const messageBox = useRef(null);

  const closeMessage = () => {
    messageBox.current.remove();
  };

  return (
    <div
      ref={messageBox}
      className={`${className} p-3  rounded  bg-secondary transition duration-1000 ease-in-out  overflow-hidden`}
    >
      {" "}
      <div className="flex gap-2 mb-2">
        <div className="flex-1">
          {/* <span className="mr-2 text-accent">
            <InformationCircleIcon className="h-6 w-6 inline-block" />
          </span>{" "} */}
          <span className="font-semibold text-primary text-base">{title}</span>
        </div>
        <div>
          <span
            onClick={() => {
              closeMessage();
            }}
            className=" border border-secondary bg-secondary brightness-125 hover:brightness-100 cursor-pointer transition duration-200   inline-block px-1 pb-1 rounded text-primary"
          >
            <XMarkIcon className="h-4 w-4 inline-block" />
          </span>
        </div>
      </div>
      {children}
    </div>
  );
};
