import { DocumentIcon, XMarkIcon } from "@heroicons/react/24/outline";
import { Checkbox, Slider } from "antd";
import * as React from "react";
import { ControlRowView } from "../../../atoms";
import { IGeneratorConfig, IUIConfig } from "../../../types";
import { rebalanceWeights, truncateText } from "../../../utils";
import AddButton from "./addbutton";

const PositivePromptsView = ({
  config,
  setConfig,
  uiConfig,
  setUiConfig,
  selectedPrompt,
  setSelectedPrompt,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  uiConfig: IUIConfig;
  setUiConfig: (uiConfig: IUIConfig) => void;
  selectedPrompt: number;
  setSelectedPrompt: (i: number) => void;
}) => {
  const pospromptsList = config.prompt?.map((prompt: string, i: number) => {
    const isSelected = i === selectedPrompt;
    return (
      <div
        className=" border-secondary text-sm rounded hover:brightness-90 duration-300 "
        key={"negpromptrow" + i}
      >
        <div
          className={`${
            isSelected ? "bg-accent text-white " : " bg-secondary  "
          } rounded`}
        >
          <div
            role={"button"}
            onClick={() => {
              setSelectedPrompt(i);
            }}
            className={`inline-block p-2  px-3 `}
          >
            {config.use_prompt_weights && (
              <span
                className="font-semibold inline-block   px-1 "
                style={{ minWidth: "40px" }}
              >
                {(config.prompt_weights &&
                  config.prompt_weights[i]?.toFixed(2)) ||
                  0}
              </span>
            )}{" "}
            {truncateText(prompt, 30) || ".."}{" "}
          </div>
          <div
            role={"button"}
            onClick={() => {
              if (config.prompt.length > 1) {
                if (config.prompt_weights) {
                  setConfig({
                    ...config,
                    prompt: config.prompt.filter((p, j) => j !== i),
                    prompt_weights: config.prompt_weights.filter(
                      (p, j) => j !== i
                    ),
                  });
                }
                setSelectedPrompt(0);
              }
            }}
            className="inline-block p-1"
          >
            <XMarkIcon className="inline-block h-5 pr-1" />
          </div>
        </div>
        {isSelected && config.use_prompt_weights && (
          <div className="  w-full">
            <Slider
              className=""
              min={0}
              max={1}
              defaultValue={config.prompt_weights[selectedPrompt]}
              step={0.05}
              onAfterChange={(value) => {
                if (config.prompt_weights) {
                  if (uiConfig.alignWeights) {
                    setConfig({
                      ...config,
                      prompt_weights: rebalanceWeights(
                        config.prompt_weights,
                        i,
                        value
                      ),
                    });
                  } else {
                    setConfig({
                      ...config,
                      prompt_weights: config.prompt_weights.map((w, j) =>
                        j === i ? value : w
                      ),
                    });
                  }
                }
              }}
            />
            <div className="text-xs -mt-1 ml-2">
              {" "}
              weight: {config.prompt_weights[i]}{" "}
            </div>
          </div>
        )}
      </div>
    );
  });

  const AddButtonClick = (prompt: string = "") => {
    {
      if (config.prompt_weights) {
        setConfig({
          ...config,
          prompt: [...config.prompt, prompt],
          prompt_weights: rebalanceWeights(
            [...config.prompt_weights, 1],
            selectedPrompt,
            config.prompt_weights[selectedPrompt]
          ),
        });
      }
      setSelectedPrompt(config.prompt.length);
    }
  };

  return (
    <div className=" pb-3 mt-1 border-b border-dashed  ">
      <div className="text-sm mb-2">
        {" "}
        <DocumentIcon className="h-4 text-accenp mr-1 inline-block" />
        Prompts{" "}
        <div className="text-xs mt-1 text-secondary">
          {" "}
          Enter multiple prompts to represent concepts
        </div>
      </div>
      <div>
        <ControlRowView
          title={null}
          className="mb-2   inline-block"
          control={
            <Checkbox
              checked={config.use_prompt_weights}
              className="text-primary"
              onChange={(e) => {
                setConfig({
                  ...config,
                  use_prompt_weights: e.target.checked,
                });
              }}
            >
              {" "}
              {`${config.use_prompt_weights ? "Disable" : "Enable"}`} prompt
              weighting
            </Checkbox>
          }
        />
        {config.use_prompt_weights && (
          <ControlRowView
            title={null}
            className="mb-2   inline-block ml-2"
            control={
              <Checkbox
                checked={uiConfig.alignWeights}
                className="text-primary"
                onChange={() => {
                  if (!uiConfig.alignWeights) {
                    setConfig({
                      ...config,
                      prompt_weights: rebalanceWeights(
                        config.prompt_weights,
                        selectedPrompt,
                        config.prompt_weights[selectedPrompt]
                      ),
                    });
                  }
                  setUiConfig({
                    ...uiConfig,
                    alignWeights: !uiConfig.alignWeights,
                  });
                }}
              >
                Align weights
              </Checkbox>
            }
          />
        )}
      </div>
      <div className="flex gap-2">
        <div>
          <div className="inline-flex flex-wrap gap-2">
            {pospromptsList}
            <AddButton
              config={config}
              setConfig={setConfig}
              configField="prompt"
              onClick={AddButtonClick}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
export default PositivePromptsView;
