import * as React from "react";
import { samples } from "../../../data/samples";
import { truncateText } from "../../utils";
import LatentFlow from "../editor/latentflow";

interface ISample {
  prompt: string;
  image: string;
}
const SamplesView = ({ data, setPrompt }: any) => {
  const serverUrl = process.env.GATSBY_API_URL;

  const sampleView = samples.map((data: ISample, i: number) => {
    return (
      <div
        role={"button"}
        onClick={() => {
          setPrompt(data.prompt);
        }}
        key={"samplerow" + i}
        className="group hover:bg-accent hover:text-white transition duration-200 flex gap-2 p-2 bg-secondary rounded"
      >
        <img
          className="w-16 h-16 group-hover:scale-150 transition duration-200 inline-block rounded "
          src={`${process.env.PREFIX_PATH_VALUE || ""}/images/gallery/default/${
            data.image
          }`}
          // src="/images/gallery/default/chief.png"
        />
        <div className="line-clamp-3 text-sm group-hover:pl-4">
          {" "}
          {data.prompt}
        </div>
      </div>
    );
  });

  return (
    <div>
      <div>
        <div className="mb-2">
          <span className="font-semibold">Need inspiration?</span> Try out one
          of the sample prompts below
        </div>
        <div className="grid grid-cols-2 md:grid-cols-5  lg:grid-cols-5 xl:grid-cols-6 gap-3">
          {" "}
          {sampleView}
        </div>
      </div>
      <div className="hidden h-64 border">
        <LatentFlow />
      </div>
    </div>
  );
};
export default SamplesView;
