import {
  ArrowUpTrayIcon,
  InboxStackIcon,
  PhotoIcon,
  XMarkIcon,
} from "@heroicons/react/24/outline";

import * as React from "react";
import { IGeneratorConfig, IStatus } from "../../../types";
import type { UploadProps } from "antd";
import { message, Upload } from "antd";
import { log } from "console";
import ImageUploadView from "./imageupload";
import ScratchPadView from "./scratchpad";

const ImagePromptsView = ({
  config,
  setConfig,
  error,
  setError,
  scratchPad,
  setScratchPad,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  error: IStatus | null;
  setError: (error: IStatus | null) => void;
  scratchPad: string[];
  setScratchPad: (scratchPad: string[]) => void;
}) => {
  const { Dragger } = Upload;

  function fileToBase64(file: any) {
    // upload image and set as init_image
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      setConfig({ ...config, init_image: reader.result, mask_image: null });
      setScratchPad([...scratchPad, reader.result]);
    };
    reader.onerror = function (error) {
      setError({ status: false, message: "File upload Error" + error });
    };
  }

  const props: UploadProps = {
    name: "file",
    className: "h-40",
    multiple: true,
    showUploadList: false,
    beforeUpload: (file) => {
      // console.log(file);
      fileToBase64(file);
      setError(null);
      return false;
    },
    onChange(info) {
      const { status } = info.file;
      if (status !== "uploading") {
        // console.log(info.file, info.fileList);
      }
      if (status === "done") {
        message.success(`${info.file.name} file uploaded successfully.`);
      } else if (status === "error") {
        message.error(`${info.file.name} file upload failed.`);
      }
    },
    onDrop(e) {
      console.log("Dropped files", e.dataTransfer.files);
    },
  };

  return (
    <div className=" pb-3  mt-4">
      <div className="text-sm mb-2">
        {" "}
        <PhotoIcon className="h-4 text-accentp inline-block mr-1" /> Image
        Prompts
        <div className="text-xs mt-1 text-secondary">
          Upload images that are used as part of your prompt. Store images in
          your scratchpad for later use.
        </div>
      </div>
      <div className="flex gap-3">
        <div className={`h-32 ${config.init_image ? "" : "flex-1"}`}>
          <Dragger {...props}>
            <ArrowUpTrayIcon className="inline-block h-8 my-2" />
            <p className="ant-upload-text">Upload Image</p>
            <span className="ant-upload-hint text-xs">
              Drag file here or click to upload
            </span>
          </Dragger>
        </div>
        <div className={` ${config.init_image ? "flex-1" : ""}`}>
          <ImageUploadView config={config} setConfig={setConfig} />
        </div>
      </div>
      <ScratchPadView
        config={config}
        setConfig={setConfig}
        scratchPad={scratchPad}
        setScratchPad={setScratchPad}
      />
    </div>
  );
};
export default ImagePromptsView;
