import { PlusIcon } from "@heroicons/react/24/outline";
import * as React from "react";

const AddButton = ({ config, setConfig, configField, onClick }: any) => {
  const [editMode, setEditMode] = React.useState(false);
  const addPromptInputRef = React.useRef<HTMLInputElement>(null);
  React.useEffect(() => {
    if (editMode) {
      addPromptInputRef.current?.focus();
    }
  }, [editMode]);

  const addPrompt = (prompt: string) => {
    if (prompt && prompt.length > 0) {
      onClick(prompt);
    }
    setEditMode(false);
  };

  return (
    <div>
      <div className="text-white text-spm   rounded">
        {" "}
        <div
          className={`inline-block ${
            editMode ? "rounded-r-none" : ""
          } px-3 transition duration-300 bg-accent p-1 rounded`}
          role={"button"}
          onClick={() => {
            if (editMode && addPromptInputRef.current?.value) {
              addPrompt(addPromptInputRef.current?.value);
            } else {
              setEditMode(!editMode);
            }
          }}
        >
          <PlusIcon className="h-4 inline-block mr-2 " />
          <div style={{ margin: "1px 0px 1px 0px" }} className="inline-block ">
            {" "}
            Add{" "}
          </div>
        </div>
        {editMode && (
          <div className="inline-block  pl-2 p-1 bg-accent rounded-r ">
            {" "}
            <input
              style={{ margin: "1px 0px 1px 0px" }}
              ref={addPromptInputRef}
              tabIndex={0}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  addPrompt(e.currentTarget.value);
                }
              }}
              className={`rounded brightness-100 hover:brightness-100 bg-white text-gray-600 px-2`}
              type={"text"}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default AddButton;
