import { AdjustmentsVerticalIcon } from "@heroicons/react/24/outline";
import { Button, Input, Modal, Slider } from "antd";
import * as React from "react";
import { ControlRowView, SecondaryButton } from "../../../atoms";
import { IGeneratorConfig } from "../../../types";

const SettingsView = ({
  config,
  setConfig,
  generate,
  isLoading,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  generate: (config: IGeneratorConfig) => void;
  isLoading: boolean;
}) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);

  //   const [generationConfig, setGenerationConfig] =
  //     React.useState<IGeneratorConfig>(config);

  return (
    <div className="text-secondary rounded ">
      <Modal
        title={
          <span>
            <AdjustmentsVerticalIcon className="h-4 text-accent inline-block mr-2 text-sm -mt-1" />
            Generation Settings
          </span>
        }
        open={isModalVisible}
        onCancel={() => {
          setIsModalVisible(false);
        }}
        onOk={() => {
          setIsModalVisible(false);
        }}
        footer={[
          <Button
            onClick={() => {
              setIsModalVisible(false);
            }}
            key="close"
            className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
            type="primary"
          >
            close
          </Button>,
          !isLoading && (
            <Button
              onClick={() => {
                generate(config);
                setIsModalVisible(false);
              }}
              key="generate"
              className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
              type="primary"
            >
              close and generate
            </Button>
          ),
        ]}
      >
        <ControlRowView
          title="Width"
          description="Width of image to be generated. Multiples of 8."
          value={config.width}
          control={
            <Slider
              min={448}
              max={1024}
              defaultValue={config.width}
              step={64}
              onAfterChange={(value) => {
                setConfig({ ...config, width: value });
              }}
            />
          }
        />
        <ControlRowView
          title="Height"
          description="Height of image to be generated. Multiples of 8."
          value={config.height}
          control={
            <Slider
              min={448}
              max={1024}
              defaultValue={config.height}
              step={64}
              onAfterChange={(value) => {
                setConfig({ ...config, height: value });
              }}
            />
          }
        />
        <ControlRowView
          title="Number of Images"
          description="Number of images to be generated."
          value={config.num_images}
          control={
            <Slider
              min={1}
              max={15}
              defaultValue={config.num_images}
              step={1}
              onAfterChange={(value) => {
                setConfig({ ...config, num_images: value });
              }}
            />
          }
        />
        {config.init_image && (
          <ControlRowView
            title="Strength"
            description="How much to transform init image. 0 uses image as is, 1 ignores image."
            value={config.strength}
            control={
              <Slider
                min={0}
                max={1}
                defaultValue={config.strength}
                step={0.05}
                onAfterChange={(value) => {
                  setConfig({ ...config, strength: value });
                }}
              />
            }
          />
        )}
        <ControlRowView
          title="Guidance Scale"
          description="Controls how closely the generation adhers to the guidance in the conditional signal (prompt, reference image). "
          value={config.guidance_scale}
          control={
            <Slider
              min={0}
              max={20}
              defaultValue={config.guidance_scale}
              step={0.1}
              onAfterChange={(value) => {
                setConfig({ ...config, guidance_scale: value });
              }}
            />
          }
        />

        <ControlRowView
          title="Inference Steps"
          description="Number of steps to run inference for each image."
          value={config.num_inference_steps}
          control={
            <Slider
              min={1}
              max={200}
              defaultValue={config.num_inference_steps}
              step={1}
              onAfterChange={(value) => {
                setConfig({ ...config, num_inference_steps: value });
              }}
            />
          }
        />

        <ControlRowView
          title="Seed"
          description="Set a specific seed (e.g 2147483647) to generate the same image every time. Leave blank to generate a random image."
          value={config.seed}
          control={
            <div className="flex mt-2">
              <Input
                // defaultValue={config.seed}
                onPressEnter={(e) => {
                  console.log(e.currentTarget.value);
                  setConfig({
                    ...config,
                    seed: parseInt(e.currentTarget.value) || null,
                  });
                }}
                placeholder={"Press enter to save."}
              />
            </div>
          }
        />

        <p className="mt-4 text-xs text-secondary">
          {" "}
          Learn more about stable diffusion parameters{" "}
          <a
            className="border-b border-accent hover:text-accent "
            target={"_blank"}
            rel={"noopener noreferrer"}
            href={"https://huggingface.co/blog/stable_diffusion"}
          >
            here
          </a>
          .
        </p>
      </Modal>

      <SecondaryButton
        onClick={() => {
          setIsModalVisible(true);
        }}
        className="w-full px-5 p-2"
      >
        <AdjustmentsVerticalIcon className="h-4 text-accent inline-block mr-2 -mt-1" />
        Settings{" "}
      </SecondaryButton>
    </div>
  );
};
export default SettingsView;
