import {
  ArrowPathIcon,
  ClipboardDocumentCheckIcon,
  TrashIcon,
} from "@heroicons/react/24/outline";
import * as React from "react";
import { IGeneratorConfig } from "../../../types";

const ScratchPadView = ({
  config,
  setConfig,
  scratchPad,
  setScratchPad,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  scratchPad: string[];
  setScratchPad: (scratchPad: string[]) => void;
}) => {
  const scratchPadList = scratchPad.map((image: string, i: number) => {
    return (
      <div className="inline-block" key={"scratchpadrow" + i}>
        <img
          role="button"
          onClick={() => {
            setConfig({
              ...config,
              init_image: image,
              mask_image: null,
            });
          }}
          // onLoad={() => {
          //   console.log("imageRef");
          // }}
          alt="image mask"
          className="h-24  object-cover rounded inline-block border"
          src={image}
        />
        <div className="flex gap-2 ">
          <div
            onClick={() => {
              setScratchPad(scratchPad.filter((_, j) => j !== i));
            }}
            role={"button"}
            className="inline"
          >
            <TrashIcon className="h-4 inline-block " />
          </div>
          <div
            onClick={() => {
              setConfig({
                ...config,
                init_image: image,
                mask_image: null,
              });
            }}
            role={"button"}
            className="inline"
          >
            <ArrowPathIcon className="h-4 inline-block " />
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="  rounded    border-secondary   mt-5">
      <div className="text-sm mb-2">
        {" "}
        <ClipboardDocumentCheckIcon className="h-4 text-accepnt inline-block" />{" "}
        Scratch Pad{" "}
        <span className="text-xs ml-2 text-accent">
          {scratchPad.length} item{scratchPad.length > 1 ? "s" : ""}{" "}
        </span>
      </div>
      <div className="flex gap-2">
        <div className="inline-flex flex-wrap gap-2">{scratchPadList}</div>
      </div>

      {!scratchPad ||
        (scratchPad && scratchPad.length == 0 && (
          <div className="border-secondary text-sm rounded">
            No items in scratchpad. Upload items to add them to scratchpad.
          </div>
        ))}
    </div>
  );
};
export default ScratchPadView;
