import {
  ArrowDownRightIcon,
  ArrowDownTrayIcon,
} from "@heroicons/react/24/outline";
import { Button, Tooltip } from "antd";
import * as React from "react";
import { ResizableBox } from "react-resizable";
import { IconButton } from "../../atoms";
import Icon from "../../icons";
import { IGeneratorConfig } from "../../types";
import { downloadBase64File } from "../../utils";

const MaskView = ({
  config,
  setConfig,
  showModal,
  setShowModal,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
  showModal: boolean;
  setShowModal: (show: boolean) => void;
}) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const containerRef = React.useRef<HTMLDivElement>(null);
  const [imageRatio, setImageRatio] = React.useState(1);
  const [eraserWidth, setEraserWidth] = React.useState(50);

  let cameraOffset: any = null;
  let isDrawing = false;
  let ctx: CanvasRenderingContext2D | null =
    canvasRef.current?.getContext("2d");

  const minImageWidth = 400;

  const drawImage = () => {
    if (ctx) {
      ctx.clearRect(0, 0, canvasRef.current?.width, canvasRef.current?.height);
      const base_image = new Image();
      base_image.src = config.init_image;
      base_image.onload = function () {
        // console.log("image width", base_image.width);
        canvasRef.current.width = base_image.width;
        canvasRef.current.height = base_image.height;
        ctx.drawImage(base_image, 0, 0);
        // ctx?.scale(0.5, 0.5);
      };
    }
  };

  React.useEffect(() => {
    if (canvasRef.current) {
      ctx = canvasRef.current.getContext("2d");
      // canvasRef.current.width = containerRef.current.clientWidth;
      // canvasRef.current.height = containerRef.current.clientHeight;
      drawImage();
      const base_image = new Image();
      base_image.src = config.init_image;
      base_image.onload = function () {
        setImageRatio(base_image.height / base_image.width);
      };
    }
  }, [config.init_image]);

  function getMousePos(canvas: any, evt: any) {
    var rect = canvas.getBoundingClientRect(), // abs. size of element
      scaleX = canvas.width / rect.width, // relationship bitmap vs. element for x
      scaleY = canvas.height / rect.height; // relationship bitmap vs. element for y

    return {
      x: (evt.clientX - rect.left) * scaleX, // scale mouse coordinates after they have
      y: (evt.clientY - rect.top) * scaleY, // been adjusted to be relative to element
    };
  }

  const startDrawing = (event: any) => {
    if (ctx != null) {
      const mousePos = getMousePos(canvasRef.current, event);
      isDrawing = true;
      ctx.beginPath();
      ctx.moveTo(mousePos.x, mousePos.y);
      ctx.lineWidth = eraserWidth;
      ctx.strokeStyle = "red";
      ctx.globalCompositeOperation = "destination-out";
    } else {
      console.log("ctx is null");
    }
  };
  const stopDrawing = () => {
    isDrawing = false;
  };
  const draw = (event: any) => {
    const mouselogo = document.getElementById("mouselogo");
    if (mouselogo) {
      const rect = event.target.getBoundingClientRect();

      mouselogo.style.left = event.clientX - rect.left - eraserWidth / 2 + "px";
      mouselogo.style.top = event.clientY - rect.top - eraserWidth / 2 + "px";
    }
    if (!isDrawing) return;

    const mousePos = getMousePos(canvasRef.current, event);
    // console.log("drawing", mousePos);

    ctx.lineCap = "round";
    ctx.lineJoin = "round";
    ctx.lineTo(mousePos.x, mousePos.y);
    ctx.stroke();
  };
  const enterCanvas = (event: any) => {
    if (ctx !== null) {
      ctx.beginPath();
    }

    const mouselogo = document.getElementById("mouselogo");
    if (mouselogo) {
      mouselogo.style.display = "block";
    }
  };

  return (
    <>
      <div className="flex">
        <div
          style={{ minHeight: "400px" }}
          ref={containerRef}
          className="w-full  "
        >
          <div className="bg-secondary p-2 rounded-t ">
            <IconButton
              icon={
                <Tooltip title="Eraser.">
                  <span className="inline-block">
                    <Icon icon="eraser" size={5} />
                  </span>
                </Tooltip>
              }
              onClick={() => {
                console.log("eraser");
              }}
            />

            <IconButton
              icon={
                <ArrowDownTrayIcon className="h-5  inline-block hover:text-accent transition duration-300" />
              }
              onClick={() => {
                let fname = "mask_file";
                fname = fname.substring(0, 100) + "_.png";
                downloadBase64File(config.init_image, fname);
              }}
            />
          </div>
          <div className="relative rounded-t-none  overflow-hidden ">
            <div
              id="mouselogo"
              style={{
                display: "none",
                width: eraserWidth + "px",
                height: eraserWidth + "px",
              }}
              className="rounded-full bg-secondary border-2 pointer-events-none h-6 w-6   absolute"
            ></div>
            <ResizableBox
              // handle={<span className="text-accent">resize</span>}
              lockAspectRatio={true}
              handle={
                <div className="absolute right-0 bottom-0 cursor-se-resize  font-semibold boprder p-3 bg-secondary">
                  <ArrowDownRightIcon className="h-4 w-4 inline-block" />
                </div>
              }
              width={minImageWidth}
              height={minImageWidth * imageRatio}
              minConstraints={[minImageWidth, minImageWidth * imageRatio]}
              maxConstraints={[900, 900 * imageRatio]}
              className="overflow-auto  w-full rounded rounded-t-none  "
            >
              <canvas
                ref={canvasRef}
                onMouseDown={startDrawing}
                onMouseUp={stopDrawing}
                onMouseMove={draw}
                onMouseEnter={enterCanvas}
                onMouseLeave={(e) => {
                  const mouselogo = document.getElementById("mouselogo");
                  if (mouselogo) {
                    mouselogo.style.display = "none";
                  }
                }}
                className="bordper  editor w-full"
                id="canvas"
              ></canvas>
            </ResizableBox>
          </div>
          <div className="mt-4 pt-3 border-t flex justify-end">
            <Button
              onClick={() => {
                drawImage();
                // setShowModal(false);
                setConfig({ ...config, mask_image: null });
                console.log("setting mask image to null");
              }}
              key="resetmask"
              className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0 mr-2 "
              type="primary"
            >
              reset
            </Button>
            <Button
              onClick={() => {
                //updateMaskImage(canvasRef.current.toDataURL());
                setConfig({
                  ...config,
                  mask_image: canvasRef.current.toDataURL(),
                });
                setShowModal(false);
              }}
              key="close"
              className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
              type="primary"
            >
              save mask
            </Button>
          </div>
        </div>
      </div>
    </>
  );
};

export default MaskView;
// mystic waterfalls inside tranquility woods
