import { Button, Empty, List, Modal, Tooltip } from "antd";
import * as React from "react";
import "react-inner-image-zoom/lib/InnerImageZoom/styles.css";
import InnerImageZoom from "react-inner-image-zoom";
import {
  ArrowDownRightIcon,
  ArrowDownTrayIcon,
  ArrowPathIcon,
  ArrowsPointingOutIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  ClipboardDocumentCheckIcon,
} from "@heroicons/react/24/outline";
import { downloadBase64File, truncateText } from "../../utils";
import { ResizableBox } from "react-resizable";
import { IGeneratorConfig } from "../../types";
import { Script } from "gatsby";
import { IconButton } from "../../atoms";

const GalleryView = ({
  data,
  config,
  setConfig,
  scratchPad,
  setScratchPad,
}: {
  data: any;
  config: IGeneratorConfig;
  setConfig: (image: any) => void;
  scratchPad: string[];
  setScratchPad: (scratchPad: string[]) => void;
}) => {
  const [isModalVisible, setIsModalVisible] = React.useState(false);
  const [selectedItem, setSelectedItem] = React.useState<number>(0);
  const galleryDivRef = React.useRef<any>(null);

  const handleOk = () => {
    setIsModalVisible(false);
  };

  React.useEffect(() => {
    if (data) {
      console.log("data loaded");
      // scroll to galleryDivRef ease
      galleryDivRef.current?.scrollIntoView({ behavior: "smooth" });
    }
  }, [data]);

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const minImageWidth = 400;

  const GalleryCard = ({ item, i }: { item: string; i: number }) => {
    return (
      <div
        className="  hover:scale-110 text-primary  drop-shadow-lg hover:drop-shadow-xl hover:z-50 transition duration-300"
        key={"row" + i}
      >
        {/* <img
        className="rounded duration-300 hover:drop-shadow-md  hover:scale-110"
        src={item}
      />{" "} */}

        <div className="flex   bg-secondary p-2 rounded-t">
          <div className="flex-1 text-center"></div>

          <IconButton
            icon={
              <Tooltip title="Add to ScratchPad.">
                <ClipboardDocumentCheckIcon className="h-6 w-6 inline-block hover:text-accent   transition duration-300" />
              </Tooltip>
            }
            onClick={() => {
              setScratchPad([...scratchPad, item]);
            }}
          />
          <IconButton
            icon={
              <Tooltip title="Remix as prompt.">
                <ArrowPathIcon className="h-6 w-6 inline-block hover:text-accent   transition duration-300" />
              </Tooltip>
            }
            onClick={() => {
              setConfig({
                ...config,
                init_image: item,
                mask_image: null,
              });
            }}
          />

          <IconButton
            icon={
              <ArrowDownTrayIcon className="h-5  inline-block hover:text-accent transition duration-300" />
            }
            onClick={() => {
              let fname = config.prompt.replace(/[^A-Z0-9]/gi, "_") + "_";
              fname = fname.substring(0, 100) + i + "_.png";
              downloadBase64File(item, fname);
            }}
          />

          <IconButton
            icon={
              <ArrowsPointingOutIcon className="h-5 inline-block hover:text-accent   transition duration-300" />
            }
            onClick={() => {
              setSelectedItem(i);
              setIsModalVisible(true);
            }}
          />
        </div>
        <InnerImageZoom
          zoomType="hover"
          zoomPreload={true}
          className="w-full rounded rounded-t-none duration-300 hover:z-50"
          src={item}
          hideHint={true}
        />
      </div>
    );
  };

  const selectedImage = data.images?.[selectedItem];

  return (
    <div
      onKeyDown={(e) => {
        if (e.key === "ArrowLeft") {
          setSelectedItem((selectedItem + 1) % data.images.length);
        }
        if (e.key === "ArrowRight") {
          setSelectedItem(
            (selectedItem - 1 + data.images.length) % data.images.length
          );
        }
      }}
      className=" "
    >
      <Modal
        title={<div className="select-none">Result</div>}
        open={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        width="min-content"
        footer={[
          <Button
            onClick={() => {
              setIsModalVisible(false);
            }}
            key="close"
            className="hover:brightness-75 hover:bg-accent brightness-100 bg-accent border-0"
            type="primary"
          >
            close
          </Button>,
        ]}
      >
        <div className="flex" tabIndex={0}>
          <div className="flex-1 ">
            <div className="relative rounded  overflow-hidden">
              <div role={"button"} className="mb-3 line-clamp-3 text-xs">
                {config.prompt}
              </div>
              <ResizableBox
                // handle={<span className="text-accent">resize</span>}
                lockAspectRatio={true}
                handle={
                  <div className="absolute right-0 bottom-0 cursor-se-resize  font-semibold boprder p-3 bg-secondary">
                    <ArrowDownRightIcon className="h-4 w-4 inline-block" />
                  </div>
                }
                width={minImageWidth}
                height={minImageWidth * (config.height / config.width)}
                minConstraints={[
                  minImageWidth,
                  minImageWidth * (config.height / config.width),
                ]}
                maxConstraints={[900, 900 * (config.height / config.width)]}
                className="overflow-auto  w-full rounded select-none "
              >
                <span className="">
                  {selectedImage && (
                    <div className="">
                      <img
                        className="w-full  h-full rounded"
                        src={selectedImage}
                      />
                    </div>
                  )}
                </span>
              </ResizableBox>
            </div>
          </div>
        </div>
        <div className="flex mt-4">
          <div
            role={"button"}
            onClick={() => {
              // cycle back through images
              setSelectedItem(
                (selectedItem - 1 + data.images.length) % data.images.length
              );
            }}
            className="flex flex-col justify-center duration-300 transition hover:text-accent "
          >
            <ChevronLeftIcon className="h-8 inline-block     rounded p-1" />{" "}
            <span className="text-xs inline-block"> previous </span>
          </div>
          <div className="flex-1   flex-center items-center flex text-center justify-center">
            {" "}
            <div
              role={"button"}
              onClick={() => {
                setConfig({
                  ...config,
                  init_image: selectedImage,
                  mask_image: null,
                });
              }}
              className="  flex justify-center flex-row"
            >
              <IconButton
                className="  block w-full"
                icon={
                  <Tooltip title="Remix as prompt.">
                    <ArrowPathIcon className="h-6 w-6 inline-block hover:text-accent   transition duration-300" />
                    <div className="text-xs">Remix</div>
                  </Tooltip>
                }
              />{" "}
            </div>
            <div
              role={"button"}
              onClick={() => {
                setScratchPad([...scratchPad, selectedImage]);
              }}
              className="  flex justify-center flex-row"
            >
              <IconButton
                className="  block w-full"
                icon={
                  <Tooltip title="Add to scratchpad.">
                    <ClipboardDocumentCheckIcon className="h-6 w-6 inline-block hover:text-accent   transition duration-300" />
                    <div className="text-xs">Scratch</div>
                  </Tooltip>
                }
              />
            </div>
          </div>
          <div
            role={"button"}
            onClick={() => {
              // cycle back through images
              setSelectedItem((selectedItem + 1) % data.images.length);
            }}
            className="flex flex-col justify-center duration-300 transition hover:text-accent "
          >
            <ChevronRightIcon className="h-8 inline-block     rounded p-1" />{" "}
            <span className="text-xs inline-block"> next </span>
          </div>
        </div>
      </Modal>
      <div className=" font-semibold text-primary py-2">
        Results
        <span className="text-xs ml-1 text-accent">{data.images.length}</span>
      </div>
      <div>
        <span className=" mb-3 inline-block text-xs text-secondary">
          {config.prompt}
        </span>
      </div>
      {/* <div className="grid grid-cols-4 gap-3">{gallery}</div> */}

      <List
        locale={{ emptyText: <Empty description="No experiments found" /> }}
        className="gap-4 flex"
        grid={{
          gutter: 10,
          xs: 1,
          sm: 2,
          md: 4,
          lg: 4,
          xl: 4,
          xxl: 6,
        }}
        dataSource={data.images}
        renderItem={(item: any, i: number) => (
          <List.Item className="exa">
            {" "}
            <GalleryCard item={item} i={i} />
          </List.Item>
        )}
        pagination={{
          pageSize: 20,
          size: "small",
          hideOnSinglePage: true,
        }}
      />
      <div ref={galleryDivRef}></div>
    </div>
  );
};
export default GalleryView;

